export class IndexModel {

	constructor(){

		this.state = {
			events_and_news_table: {
				events_table: [],
				news_table: [],
				loading: false,
				error: null
			},
			user_email:{
				loading: false,
				user_email_address: '',
				user_email_submitted: null
			} 
		}

		this.listeners = [];

		this.images = [];

	}

    setState = (newState) =>{ 
    	this.state = {...this.state,...newState};
    	this.notifyListeners();
    	console.log(this.state)
    	return this.state
    }

    registerListener = (listener) => {

    	this.listeners = [...this.listeners, listener]

    	listener(this.state)
    }

    notifyListeners = () => {
    	this.listeners.forEach((listener)=>{
    		listener(this.state)
		})
    }


	index_model_get_user_email = () => {
		return this.user_email
	}

	index_model_get_user_email_submitted = () => {
		return this.user_email_submitted
	}

	submit_user_email = (email) => {

		this.setState({
			user_email:{
				loading: true,
				user_email_address: email,
				user_email_submitted: null
			} 
		})	

		if (process.env.NODE_ENV === "production") {
			fetch("https://abbey-road-api.onrender.com/api/index-page/submit_user_email",{
			method: 'post',
			headers: {
    			'Content-Type': 'application/json',  
  			},
			body: JSON.stringify({
				user_email: this.state.user_email.user_email_address
			})
		})
		.then((res)=>{
			return res.json()
		})
		.then((res=>{
			console.log(res)

			if(res.success && res.data){
				this.setState({
					user_email:{
						loading: false,
						user_email_address: res.data,
						user_email_submitted: true
					} 
				})	
			}
		}))
		.catch((err)=>{
			console.log(err);
				this.setState({
					user_email:{
						loading: false,
						user_email_address: email,
						user_email_submitted: false
					} 
				})	
		})

		}
		else{
		fetch("http://localhost:3001/api/index-page/submit_user_email",{
			method: 'post',
			headers: {
    			'Content-Type': 'application/json',  
  			},
			body: JSON.stringify({
				user_email: this.state.user_email.user_email_address
			})
		})
		.then((res)=>{
			return res.json()
		})
		.then((res=>{
			console.log(res)

			if(res.success && res.data){
				this.setState({
					user_email:{
						loading: false,
						user_email_address: res.data,
						user_email_submitted: true
					} 
				})	
			}
		}))
		.catch((err)=>{
			console.log(err);
				this.setState({
					user_email:{
						loading: false,
						user_email_address: email,
						user_email_submitted: false
					} 
				})	
		})
		}


		


	}

	fetch_latest_news_and_events = () => {

		this.setState({ events_and_news_table: {
				events_table: [],
				news_table: [],
				loading: true,
				error: null
			}
		})	    	


		if (process.env.NODE_ENV === "production") {
		fetch("https://abbey-road-api.onrender.com/api/index-page/events_and_news")
		.then((res)=>{
			return res.json();
		})
		.then((res)=>{

			if(res.status && res.data){

				this.setState({events_and_news_table: {
						events_table: [...res.data.events_table],
						news_table: [...res.data.news_table],
						loading: false,
						error:null
					}
				})	    		

			}
		})
		.catch((err)=>{

			this.setState({ events_and_news_table: {
				loading: false,
				error: err,
				events_table: [],
				news_table: [],
				}
			})	    		
		})

		}
		else{
		fetch("http://localhost:3001/api/index-page/events_and_news")
		.then((res)=>{
			return res.json();
		})
		.then((res)=>{

			if(res.status && res.data){

				this.setState({events_and_news_table: {
						events_table: [...res.data.events_table],
						news_table: [...res.data.news_table],
						loading: false,
						error:null
					}
				})	    		

			}
		})
		.catch((err)=>{

			this.setState({ events_and_news_table: {
				loading: false,
				error: err,
				events_table: [],
				news_table: [],
				}
			})	    		
		})
		}
	}

	get_images = () => {

		if (process.env.NODE_ENV === "production") {
		return fetch("https://abbey-road-api.onrender.com/api/resident-involvment-page/get_all_images")
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{
			if(res.success && res.data.length > 0){
				console.log(res)
				this.images = res.data
				return Promise.resolve({
					status: true,
					images: this.images
				})
			}
			else{
				return Promise.reject({status:false})
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.reject({status:false})
		})
		}
		else{
		return fetch("http://localhost:3001/api/resident-involvment-page/get_all_images")
		.then((res)=>{
			return res.json()
		})
		.then((res)=>{
			if(res.success && res.data.length > 0){
				console.log(res)
				this.images = res.data
				return Promise.resolve({
					status: true,
					images: this.images
				})
			}
			else{
				return Promise.reject({status:false})
			}
		})
		.catch((err)=>{
			console.log(err)
			return Promise.reject({status:false})
		})
		}

	}
}